import jsons from '../../static/json/items.json';
export default {
	data() { 
		return {
			arr: [],
			apirce:true
		};
	},
	onReady() {
		this.arr = jsons.item;
	 },
	methods: {
		pirce(){
			this.arr = jsons.item.sort((a, b) =>
				  b.cost-a.cost 
			);
			this.apirce=false
		},
		repirce(){
			this.arr = jsons.item.sort((a, b) =>
				  a.cost-b.cost 
			);
			this.apirce=true
		},
		sell() {
			this.arr = jsons.item.sort((a, b) =>
				  b.sales-a.sales 
			);
		},
		filterbrand(other) {
			this.arr = jsons.item.filter(one => {
				return one.brand.match(other);
			});
		},
		filtercolor(other) {
			this.arr = jsons.item.filter(one => {
				return one.color.match(other);
			});
		},
		apple() {
			this.filterbrand('Apple');
		},
		Beats() {
			this.filterbrand('Beats');
		},
		Bose() {
			this.filterbrand('Bose');
		},
		Bose() {
			this.filterbrand('Bose');
		},
		dell() {
			this.filterbrand('dell');
		},
		white() {
			this.filtercolor('白色');
		},
		red() {
			this.filtercolor('红色');
		},
		black() {
			this.filtercolor('黑色');
		},
		joincar(id) {
			this.$store.dispatch('joinstates', id);
		},

		toitem(id) {
			console.log(id);
			uni.navigateTo({
				url: '../item/item?i=' + id
			});
		}
	}
};
